<template>
  <a-layout>
    <a-layout-header class="g-header">
      <my-progress
        :percent="needPercent"
        :subPercent="usedPercent"
        :strokeWidth="8"
        :class="[breakpoint ? 'progressBreak' : 'progress']"
      />
      <input
        ref="inputFile"
        type="file"
        multiple="multiple"
        style="display:none;width:0px;height:0px"
        @change="inputFile"
      />
      <div ref="uploadArea" class="g-uploadArea">
        <a-button
          class="g-upload"
          type="dashed" :size="size" ref="uploadBtn" @click="chooseFile"
          block>
          <p class="g-upload-drag-icon">
            <a-icon class="icon" type="inbox"/>
          </p>
          <p class="g-upload-text">点击或拖拽文件到此处上传</p>
          <p class="g-upload-hint">支持单个或多个文件，严禁上传被禁止的文件</p>
        </a-button>
      </div>
    </a-layout-header>
    <a-layout-content class="g-content">
      <a-list>
        <upload-item
          v-for="(item, index) in fileList"
          :itemData="item"
          :index="index"
          :key="item.file.lastModified + '' + index"
          :disable="disable"
          @removeFile="removeFile"
        ></upload-item>
      </a-list>
    </a-layout-content>
    <a-layout-footer class="g-footer">
      <a-button icon="upload" :size="size" @click="uploadFile" block>上传</a-button>
    </a-layout-footer>
  </a-layout>
</template>
<script>
import UploadItem from '../components/UploadItem'
import MyProgress from '../components/MyProgress'

export default {
  name: 'UploadPanel',
  components: {
    UploadItem,
    MyProgress
  },
  props: {
    breakpoint: Boolean
  },
  data: function () {
    return {
      size: 'default',
      fileList: [],
      uploadIndex: 0,
      disable: false,
      totalSpace: 0,
      usedSpace: 0,
      needSpace: 0,
      usedPercent: 0,
      needPercent: 0
    }
  },
  computed: {},
  watch: {
    fileList: function () {
      this.computeSizePercent()
    },
    usedSpace: function () {
      this.computeSizePercent()
    }
  },
  mounted: function () {
    this.$refs.uploadArea.ondragleave = e => {
      e.preventDefault() // 阻止离开时的浏览器默认行为
    }
    this.$refs.uploadArea.ondrop = e => {
      e.preventDefault() // 阻止拖放后的浏览器默认行为
      const data = e.dataTransfer.files // 获取文件对象
      if (data.length < 1) {
        return // 检测是否有文件拖拽到页面
      }
      for (let i = 0; i < e.dataTransfer.files.length; i++) {
        if (e.dataTransfer.files[i].type === '') {
          continue
        }
        const fileItem = {
          file: null,
          info: {
            progress: 0,
            status: 'upload',
            type: ''
          }
        }
        const fileListlength = this.fileList.length
        if (fileListlength < 1) {
          fileItem.file = e.dataTransfer.files[i]
          fileItem.info.type = this.getFileType(e.dataTransfer.files[i].name)
          this.fileList.push(fileItem)
        } else {
          let hasfile = true
          for (let m = 0; m < fileListlength; m++) {
            if (
              e.dataTransfer.files[i].name === this.fileList[m].name &&
              e.dataTransfer.files[i].lastModified === this.fileList[m].lastModified
            ) {
              hasfile = false
              break
            }
          }
          if (hasfile) {
            fileItem.file = e.dataTransfer.files[i]
            fileItem.info.type = this.getFileType(e.dataTransfer.files[i].name)
            this.fileList.push(fileItem)
          }
        }
      }
    }
    this.$refs.uploadArea.ondragenter = e => {
      e.preventDefault() // 阻止拖入时的浏览器默认行为
    }
    this.$refs.uploadArea.ondragover = e => {
      e.preventDefault() // 阻止拖来拖去的浏览器默认行为
    }
    this.getDiskInfo()
  },
  methods: {
    computeSizePercent: function () {
      const length = this.fileList.length
      const data = this.fileList
      let size = 0
      for (let i = 0; i < length; i++) {
        if (data[i].info.status !== 'upload') continue
        size = size + data[i].file.size
      }
      this.needSpace = this.usedSpace + size
      if (this.needSpace > this.totalSpace) {
        this.needPercent = 100
        this.$message.error('上传内容过大，请取消部分上传内容', 10)
      } else {
        this.needPercent = (this.needSpace / this.totalSpace) * 100
      }
      this.usedPercent = (this.usedSpace / this.totalSpace) * 100
    },
    getDiskInfo: function () {
      this.axios.post('/api/getDiskInfo').then(response => {
        this.usedSpace = response.usedSpace
        this.totalSpace = response.totalSpace
      })
    },
    getFileType: function (name) {
      return name
        .split('.')
        .pop()
        .toLowerCase()
    },
    inputFile: function () {
      const data = this.$refs.inputFile.files // 获取文件对象
      if (data.length < 1) {
        return
      }
      for (let i = 0; i < data.length; i++) {
        if (data[i].type === '') {
          continue
        }
        const fileItem = {
          file: null,
          info: {
            progress: 0,
            status: 'upload',
            type: ''
          }
        }
        const fileCount = this.fileList.length
        if (fileCount < 1) {
          fileItem.file = data[i]
          fileItem.info.type = this.getFileType(data[i].name)
          this.fileList.push(fileItem)
        } else {
          let hasFile = true
          for (let m = 0; m < fileCount; m++) {
            if (data[i].name === this.fileList[m].name && data[i].lastModified === this.fileList[m].lastModified) {
              hasFile = false
              break
            }
          }
          if (hasFile) {
            fileItem.file = data[i]
            fileItem.info.type = this.getFileType(data[i].name)
            this.fileList.push(fileItem)
          }
        }
      }
      this.$refs.inputFile.value = ''
    },
    chooseFile: function () {
      this.$refs.inputFile.dispatchEvent(new MouseEvent('click'))
    },
    removeFile: function (index) {
      if (index <= this.uploadIndex && this.uploadIndex !== 0) {
        this.uploadIndex--
      }
      this.fileList.splice(index, 1)
    },
    uploadFile: function () {
      if (this.fileList.length > 0 && this.fileList.length > this.uploadIndex) {
        this.disable = true
        const config = {
          headers: {'Content-Type': 'multipart/form-data'},
          onUploadProgress: progressEvent => {
            var complete = ((progressEvent.loaded / progressEvent.total) * 100) | 0
            this.fileList[this.uploadIndex].info.progress = complete
            // if (complete === 100) {
            //   this.fileList[this.uploadindex].info.status = 'sub';
            //   this.uploadindex++;
            //   if (this.uploadindex < this.fileList.length) {
            //     this.uploadFile();
            //   } else {
            //     this.disable = false;
            //   }
            // }
          }
        }
        const params = new FormData()
        params.append('uploadFile', this.fileList[this.uploadIndex].file, this.fileList[this.uploadIndex].file.name)
        this.axios
          .post('/upload', params, config)
          .then(response => {
            this.fileList[this.uploadIndex].info.status = 'sub'
            this.uploadIndex++
            if (this.uploadIndex < this.fileList.length) {
              this.uploadFile()
            } else {
              this.disable = false
            }
            this.getDiskInfo()
          })
          .catch(err => {
            console.error(err)
            this.fileList[this.uploadIndex].info.status = 'err'
            this.uploadIndex++
            this.$message.error('上传失败，请检查网络连接、浏览器地址或是否已关闭文件服务！', 5)
            if (this.uploadIndex < this.fileList.length) {
              this.uploadFile()
            } else {
              this.disable = false
            }
          })
      }
    }
  }
}
</script>
<style lang="less">
.g-header {
  background: white;
  height: auto;
  padding: 4px 10px;

  .progress {
    margin: 10px 0px;
  }

  .progressBreak {
    margin: 10px 0px 10px 22px;
  }
}

.g-content {
  background-color: white;
  padding: 0px 10px;
  overflow-x: hidden;
  overflow-y: auto;
}

.g-footer {
  background: white;
  padding: 5px 10px;
}

.g-upload {
  padding-top: 5px;
  height: auto;

  p {
    line-height: 16px;
  }

  .g-upload-drag-icon {
    .icon {
      font-size: 48px;
    }
  }

  .g-upload-text {
    font-size: 16px;
    color: black;
  }

  .g-upload-hint {
    font-size: 14px;
    color: gray;
  }
}

.g-uploadArea {
  display: inline-block;
  width: 100%;
}
</style>
