<template>
  <a-layout>
    <a-layout-header class="g-header">
      <my-progress
        class="progress"
        :percent="computeDiskInfo"
        :strokeWidth="8"
        :class="[breakpoint ? 'progressBreak' : 'progress']"
      />
      <a-row style="line-height:1.5">
        <a-col :xs="12">
          <a-button-group>
            <a-button type="primary" icon="check" @click="checkAll"/>
            <a-button type="primary" icon="plus"
                      @click="mkdirVisible = !mkdirVisible"
            >{{ breakpoint ? '' : '新建文件夹' }}
            </a-button>
            <a-modal
              title="新建文件夹"
              v-model="mkdirVisible"
              :confirmLoading="mkdirConfirmLoading"
              :okButtonProps="{ props: { disabled: mkdirOkDisable } }"
              @ok="handleMkdirOk"
              @cancel="mkdirName = ''"
              :maskClosable="false"
            >
              <a-input
                ref="dirInput"
                placeholder="新文件夹名称"
                v-model="mkdirName"
                @change="checkDirName"
                @blur="checkDirName"
              />
              <p v-if="mkdirTipsVisible" style="color:red">
                输入正确的文件夹名称
              </p>
            </a-modal>
          </a-button-group>
        </a-col>
        <a-col :xs="12">
          <div style="float:right">
            <a-button-group>
              <a-button type="primary" icon="cloud-upload" @click="uploadVisible = true"/>
              <a-modal
                title="上传文件"
                v-model="uploadVisible"
                @ok="uploadFileOk"
                @cancel="cancelUpload"
                :confirmLoading="uploadConfirmLoading"
                class="uploadModal"
              >
                <div>
                  <input ref="inputFile" type="file" style="display:none;width:0px;height:0px" @change="inputFile"/>
                  <a-button @click="chooseUploadFile" :disabled="uploadBtnDisable">
                    <a-icon type="upload"/>
                    Upload
                  </a-button>
                  <span>{{ uploadFile === null ? '暂未选择文件' : uploadFile.name }}</span>
                </div>

                <p>文件会上传在当前文件夹下</p>
                <p>一次只能上传一个文件</p>
              </a-modal>

              <a-button type="primary" icon="cloud-download" @click="downloadSelectFile"/>
              <a-modal></a-modal>

              <a-button type="primary" icon="delete" @click="delFileVisible = true"/>
              <a-modal
                title="确认"
                @ok="removeAllSelectFile"
                v-model="delFileVisible"
                :confirmLoading="delFileConfirmLoading"
              >
                <p>确认是否删除选中的文件与文件夹</p>
              </a-modal>
            </a-button-group>
          </div>
        </a-col>
      </a-row>
      <a-row type="flex" justify="space-around" align="middle" class="filePanel">
        <a-col :xs="15" :sm="11">文件名</a-col>
        <a-col :xs="6">文件大小</a-col>
        <a-col :xs="0" :sm="6">最后修改时间</a-col>
        <a-col :xs="3" :sm="1" style="text-align: center">更多</a-col>
      </a-row>
    </a-layout-header>
    <a-layout-content class="g-content">
      <a-spin :spinning="spinning">
        <a-icon slot="indicator" type="loading" style="font-size: 24px" spin/>
        <file-item type="percent" v-if="hasPercent" @click.native="backPercent"/>
        <file-item
          v-for="(item, index) in dirList"
          :key="item.name + '_' + index"
          :itemData="item"
          :index="index"
          type="folder"
          :breakpoint="breakpoint"
          @chooseFileItem="chooseFileItem"
          @click.native="viewDir(item)"
          :isChecked="isChecked('folder', index)"
          @delFile="showDelModal"
          @removeFile="showRemoveModal"
          @renameFile="showRenameModal"
          @downloadFile="downloadFile"
          @openFile="openFile"
        />
        <file-item
          v-for="(item, index) in fileList"
          :key="item.name + '_' + index"
          :index="index"
          :itemData="item"
          type="file"
          :breakpoint="breakpoint"
          :isChecked="isChecked('file', index)"
          @chooseFileItem="chooseFileItem"
          @delFile="showDelModal"
          @removeFile="showRemoveModal"
          @renameFile="showRenameModal"
          @downloadFile="downloadFile"
          @openFile="openFile"
        />
      </a-spin>
      <a-modal
        title="删除文件"
        v-model="ddelFileVisible"
        :confirmLoading="ddelFileConfirmLoading"
        @ok="delFileOK"
        @cancel="cancelDel"
        :maskClosable="false"
      >
        <p>是否删除文件</p>
        <p>{{ getCheckFileName }}</p>
      </a-modal>
      <a-modal
        title="移动文件"
        v-model="removeFileVisible"
        :confirmLoading="removeFileConfirmLoading"
        :okButtonProps="{ props: { disabled: hasRemoveSelect } }"
        @ok="removeFileOk"
        @cancel="cancelRemove"
      >
        <p>{{ getCheckFileName }}</p>
        <a-tree :loadData="onLoadData" :treeData="treeData" @select="selectToDir"/>
      </a-modal>
      <a-modal
        title="文件重命名"
        v-model="renameFileVisible"
        :confirmLoading="renameFileConfirmLoading"
        :okButtonProps="{ props: { disabled: renameOkDisable } }"
        @ok="renameFileOk"
        @cancel="cancelRename"
        :maskClosable="false"
      >
        <p>{{ getCheckFileName }}</p>
        <a-input placeholder="新文件名称" v-model="renameName" @change="checkFileName" @blur="checkFileName"/>
        <p v-if="renameTipsVisible">输入合格的文件名</p>
      </a-modal>
    </a-layout-content>
  </a-layout>
</template>
<script>
import MyProgress from '../components/MyProgress'
import FileItem from '../components/FileItem'

import {baseUrl} from '@/config'

export default {
  name: 'Manage',
  components: {
    MyProgress,
    FileItem
  },
  props: {
    breakpoint: Boolean
  },
  data: function () {
    return {
      dirs: [],
      fileList: [],
      dirList: [],
      mkdirName: '',
      mkdirVisible: false,
      mkdirConfirmLoading: false,
      mkdirTipsVisible: false,
      mkdirOkDisable: true,

      uploadFile: null,
      uploadVisible: false,
      uploadConfirmLoading: false,
      uploadBtnDisable: false,

      delFileVisible: false,
      delFileConfirmLoading: false,

      spinning: false,

      selectDir: [],
      selectFile: [],

      ddelFileVisible: false,
      ddelFileConfirmLoading: false,

      renameFileVisible: false,
      renameFileConfirmLoading: false,
      renameName: '',
      renameOkDisable: true,
      renameTipsVisible: false,

      removeFileVisible: false,
      removeFileConfirmLoading: false,
      hasRemoveSelect: true,
      newPath: [],

      ddselectType: '',
      // 选中的文件下标
      checkFileIndex: null,

      treeData: [],
      totalSpace: 0,
      usedSpace: 0
    }
  },
  computed: {
    hasPercent() {
      if (this.dirs.length <= 1) {
        return false
      } else {
        return true
      }
    },
    // 选中的文件名
    getCheckFileName: function () {
      if (this.fileList.length > 0 || this.dirList.length > 0) {
        if (this.ddselectType === 'file') {
          return this.fileList[this.checkFileIndex].name
        } else if (this.ddselectType === 'folder') {
          return this.dirList[this.checkFileIndex].name
        }
      }
      return '没有文件被选中'
    },
    computeDiskInfo: function () {
      return (this.usedSpace / this.totalSpace) * 100
    }
  },
  mounted: function () {
    this.axios.post('/api/getUploadDirInfo').then(response => {
      this.dirs = response.dirs
      this.fileList = response.allFileInfo.files
      this.dirList = response.allFileInfo.directories
    })
    this.getDiskInfo()
  },
  methods: {
    deleteWarning: function () {
    },
    handleMkdirOk: function () {
      this.mkdirConfirmLoading = true
      const name = this.$refs.dirInput.value
      if (name === '') {
        this.mkdirTipsVisible = true
      } else {
        this.mkdirTipsVisible = false
        this.axios
          .post('/api/mkdir', {
            path: this.dirs,
            dirname: name
          })
          .then(response => {
            if (response.data === 'suss') this.mkdirVisible = false
            this.mkdirConfirmLoading = false
            this.mkdirName = ''
            this.renewDirInfo()
          })
      }
    },
    checkDirName: function (e) {
      if (/[\\/:*?"<>|]/.test(this.mkdirName) || this.mkdirName === '') {
        this.mkdirTipsVisible = true
        this.mkdirOkDisable = true
      } else {
        this.mkdirTipsVisible = false
        this.mkdirOkDisable = false
      }
    },
    backPercent: function (e) {
      this.dirs.pop()
      this.spinning = true
      this.axios.post('/api/getDirAllInfo', {path: this.dirs}).then(response => {
        this.fileList = response.files
        this.dirList = response.directories
        this.spinning = false
        this.spinning = false
        this.selectDir = []
        this.selectFile = []
        this.ddselectType = ''
        this.checkFileIndex = null
      })
      this.getDiskInfo()
    },
    viewDir: function (e) {
      this.dirs.push(e.name)
      this.spinning = true
      this.axios.post('/api/getDirAllInfo', {path: this.dirs}).then(response => {
        this.fileList = response.files
        this.dirList = response.directories
        this.spinning = false
        this.selectDir = []
        this.selectFile = []
        this.ddselectType = ''
        this.checkFileIndex = null
      })
      this.getDiskInfo()
    },
    renewDirInfo: function () {
      this.spinning = true
      this.axios.post('/api/getDirAllInfo', {path: this.dirs}).then(response => {
        this.fileList = response.files
        this.dirList = response.directories
        this.spinning = false
        this.ddselectType = ''
        this.checkFileIndex = null
      })
      this.getDiskInfo()
    },
    chooseFileItem: function (type, index, action) {
      if (type === 'file') {
        const length = this.selectFile.length
        if (action) {
          for (let i = 0; i < length; i++) {
            if (this.selectFile[i] === index) {
              return
            }
          }
          this.selectFile.push(index)
        } else {
          for (let i = 0; i < this.selectFile.length; i++) {
            if (this.selectFile[i] === index) {
              this.selectFile.splice(i, 1)
              i--
            }
          }
        }
      }
      if (type === 'folder') {
        const length = this.selectDir.length
        if (action) {
          for (let i = 0; i < length; i++) {
            if (this.selectDir[i] === index) {
              return
            }
          }
          this.selectDir.push(index)
        } else {
          for (let i = 0; i < this.selectDir.length; i++) {
            if (this.selectDir[i] === index) {
              this.selectDir.splice(i, 1)
              i--
            }
          }
        }
      }
    },
    removeAllSelectFile: function () {
      console.log('removeAllSelectFile')
      const fileList = []
      let length = this.selectDir.length
      for (let i = 0; i < length; i++) {
        fileList.push(this.dirList[this.selectDir[i]].name)
      }
      length = this.selectFile.length
      for (let i = 0; i < length; i++) {
        fileList.push(this.fileList[this.selectFile[i]].name)
      }
      this.delFileConfirmLoading = true
      this.axios.post('/api/delAllFile', {path: this.dirs, names: fileList}).then(response => {
        this.delFileConfirmLoading = false
        this.delFileVisible = false
        this.selectDir = []
        this.selectFile = []
        this.renewDirInfo()
      })
    },
    chooseUploadFile: function () {
      this.$refs.inputFile.dispatchEvent(new MouseEvent('click'))
    },
    inputFile: function () {
      const data = this.$refs.inputFile.files // 获取文件对象
      if (data.length < 1) {
        return
      }
      this.uploadFile = data[0]
    },
    uploadFileOk: function () {
      this.uploadConfirmLoading = true
      this.uploadBtnDisable = true
      const config = {
        headers: {'Content-Type': 'multipart/form-data'}
      }
      const params = new FormData()
      params.append('path', this.dirs)
      params.append('uploadFile', this.uploadFile, this.uploadFile.name)
      this.axios.post('/api/uploadToDir', params, config).then(response => {
        this.uploadConfirmLoading = false
        this.uploadBtnDisable = false
        this.uploadVisible = false
        this.$refs.inputFile.value = ''
        this.uploadFile = null
        this.renewDirInfo()
      })
    },
    cancelUpload: function () {
      this.$refs.inputFile.value = ''
      this.uploadFile = null
    },
    isChecked(type, index) {
      if (type === 'file') {
        const length = this.selectFile.length
        for (let i = 0; i < length; i++) {
          if (this.selectFile[i] === index) return true
        }
      } else if (type === 'folder') {
        const length = this.selectDir.length
        for (let i = 0; i < length; i++) {
          if (this.selectDir[i] === index) return true
        }
      }
      return false
    },
    checkAll: function () {
      let length = this.dirList.length
      for (let i = 0; i < length; i++) {
        this.selectDir.push(i)
      }
      length = this.fileList.length
      for (let i = 0; i < length; i++) {
        this.selectFile.push(i)
      }
    },
    showRemoveModal: function (type, index) {
      this.ddselectType = type
      this.checkFileIndex = index
      this.treeData.push({title: this.dirs[0], key: '0'})
      this.removeFileVisible = true
    },
    removeFileOk: function () {
      this.removeFileConfirmLoading = true
      console.log(this.getCheckFileName)
      this.axios
        .post('/api/removeTo', {
          oldPath: this.dirs,
          newPath: this.newPath,
          name: this.getCheckFileName
        })
        .then(response => {
          this.removeFileConfirmLoading = false
          this.removeFileVisible = false
          this.treeData = []
          this.hasRemoveSelect = true
          this.renewDirInfo()
        })
    },
    cancelRemove: function () {
      this.treeData = []
    },
    onLoadData: function (treeNode) {
      const dirIndex = treeNode.eventKey.split('-')
      const paths = []
      let data = this.treeData
      for (let i = 0; i < dirIndex.length; i++) {
        paths.push(data[parseInt(dirIndex[i])].title)
        data = data[parseInt(dirIndex[i])].children
      }
      return this.axios.post('/api/getDirs', {path: paths}).then(response => {
        if (response.data !== 'fail') {
          const children = []
          for (let i = 0; i < response.length; i++) {
            children.push({
              title: response.data[i],
              key: treeNode.eventKey + '-' + i
            })
          }
          treeNode.dataRef.children = children
          this.treeData = [...this.treeData]
        }
      })
    },
    selectToDir: function (selectedKeys, e) {
      const dirindex = selectedKeys[0].split('-')
      const paths = []
      let data = this.treeData
      for (let i = 0; i < dirindex.length; i++) {
        paths.push(data[parseInt(dirindex[i])].title)
        data = data[parseInt(dirindex[i])].children
      }
      this.newPath = paths
      this.hasRemoveSelect = false
    },
    showDelModal: function (type, index) {
      this.ddelFileVisible = true
      this.ddselectType = type
      this.checkFileIndex = index
    },
    delFileOK: function () {
      const fileList = []
      if (this.ddselectType === 'file') {
        fileList.push(this.fileList[this.checkFileIndex].name)
      } else if (this.ddselectType === 'folder') {
        fileList.push(this.dirList[this.checkFileIndex].name)
      }
      this.ddelFileConfirmLoading = true
      this.axios.post('/api/delAllFile', {path: this.dirs, names: fileList}).then(response => {
        this.ddelFileVisible = false
        this.ddelFileConfirmLoading = false
        this.selectDir = []
        this.selectFile = []
        this.renewDirInfo()
      })
    },

    cancelDel: function () {
    },
    showRenameModal: function (type, index) {
      this.renameFileVisible = true
      this.ddselectType = type
      this.checkFileIndex = index
    },
    checkFileName: function () {
      if (/^\.|[\\/:*?"<>]|\.$/.test(this.renameName)) {
        this.renameOkDisable = true
        this.renameTipsVisible = true
      } else {
        this.renameTipsVisible = false
        this.renameOkDisable = false
      }
    },
    renameFileOk: function () {
      this.renameFileConfirmLoading = true
      let oldName = ''
      if (this.ddselectType === 'file') {
        oldName = this.fileList[this.checkFileIndex].name
      } else if (this.ddselectType === 'folder') {
        oldName = this.dirList[this.checkFileIndex].name
      }
      this.axios
        .post('/api/rename', {
          path: this.dirs,
          oldName: oldName,
          newName: this.renameName
        })
        .then(response => {
          this.renameFileVisible = false
          this.renameFileConfirmLoading = false
          this.renameOkDisable = false
          this.renameName = ''
          this.renameTipsVisible = false
          this.selectDir = []
          this.selectFile = []
          this.renewDirInfo()
        })
    },
    cancelRename: function () {
      this.renameName = ''
    },
    downloadFile: function (type, index) {
      console.log(this.fileList)
      this.ddselectType = type
      this.checkFileIndex = index

      let dirPath = this.dirs.join('/');
      const downloadFilename = this.fileList[index].name
      let fileNameEncode = encodeURIComponent(dirPath + '/' + downloadFilename);
      window.open(baseUrl + `/api/download?path=${fileNameEncode}`, '_blank')
    },
    openFile: function (type, index) {
      this.ddselectType = type
      this.checkFileIndex = index

      let dirPath = this.dirs.join('/');
      const downloadFilename = this.fileList[index].name
      let fileNameEncode = encodeURIComponent(dirPath + '/' + downloadFilename);
      window.open(baseUrl + `/api/openFile?path=${fileNameEncode}`, '_blank')
    },
    downloadSelectFile: function () {
      if (this.selectDir.length > 0) {
        this.$message.error('无法下载文件夹')
      } else {
        const downFiles = this.selectFile
        console.log(downFiles)
        for (let i = 0; i < downFiles.length; i++) {
          const downloadFilename = this.fileList[parseInt(downFiles[i])].name
          window.open(baseUrl + `/api/download?path=${downloadFilename}`, '_blank')
        }
      }
    },
    getDiskInfo: function () {
      this.axios.post('/api/getDiskInfo').then(response => {
        this.usedSpace = response.usedSpace
        this.totalSpace = response.totalSpace
      })
    }
  }
}
</script>
<style lang="less">
.g-header {
  background: white;
  height: auto;
  padding: 4px 10px;

  .progress {
    margin: 10px 0px;
  }

  .progressBreak {
    margin: 10px 0px 10px 22px;
  }
}

.g-content {
  background-color: white;
  padding: 0px 10px;
  overflow-x: hidden;
  overflow-y: auto;
}

.g-footer {
  background: white;
  padding: 5px 10px;
}

.filePanel {
  background-color: white;
  margin: 5px 0px;
  padding: 8px 0px 8px 10px;
  line-height: 1.5;
}

.uploadModal {
  p {
    margin-bottom: 0;
  }

  span {
    margin-left: 5px;
  }
}
</style>
