import Vue from 'vue'
import {
  Breadcrumb,
  Button,
  Checkbox,
  Col,
  Divider,
  Icon,
  Input,
  Layout,
  List,
  Menu,
  Message,
  Modal,
  Popover,
  Row,
  Spin,
  Tree
} from 'ant-design-vue'
import App from './App'
import request from '@/utils/request'
import router from './router'

Vue.use(Layout)
Vue.use(Menu)
Vue.use(Icon)
Vue.use(Button)
Vue.use(List)
Vue.use(Row)
Vue.use(Col)
Vue.use(Checkbox)
Vue.use(Breadcrumb)
Vue.use(Popover)
Vue.use(Modal)
Vue.use(Input)
Vue.use(Spin)
Vue.use(Tree)
Vue.use(Divider)
Vue.prototype.axios = request
Vue.prototype.$message = Message
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})
