<template>
  <div class="uploadItem">
    <a-row type="flex" justify="space-around" align="middle">
      <a-col :xs="5" class="name">{{ this.itemData.file.name }}</a-col>
      <a-col :xs="4" class="type">{{ this.itemData.info.type }}</a-col>
      <a-col :xs="12" :sm="14">
        <my-progress
          :percent="this.itemData.info.progress"
          :subPercent="this.itemData.info.status === 'sub' ? 100 : 0"
          :errPercent="this.itemData.info.status === 'err' ? 100 : 0"
          :strokeWidth="8"
        />
      </a-col>
      <a-col :xs="3" :sm="1" style="text-align: center">
        <a-button shape="circle" icon="close" size="small" :disabled="this.disable" @click="removeFile(index)"/>
      </a-col>
    </a-row>
  </div>
</template>
<script>
import MyProgress from './MyProgress.vue'

export default {
  name: 'UploadItem',
  components: {
    MyProgress
  },
  data: function () {
    return {}
  },
  props: {
    itemData: Object,
    disable: Boolean,
    index: Number
  },
  methods: {
    removeFile: function () {
      this.$emit('removeFile', this.index)
    }
  }
}
</script>
