<template>
  <div class="fileItem">
    <a-row type="flex" justify="space-around" align="middle">
      <span v-if="type === 'percent'" style="float:left">
        <a-icon type="up"/>
      </span>
      <template v-else>
        <a-col :xs="15" :sm="11" class="name">
          <a-checkbox @change="chooseFileItem" @click.stop :checked="isChecked"></a-checkbox>
          <a-icon :theme="'filled'" style="font-size:24px;margin:0px 5px" :type="type === 'file' ? 'file' : 'folder'"/>
          {{ this.itemData.name }}
        </a-col>
        <a-col :xs="6" class="size">{{ type === 'file' ? getFileSize : '-' }}</a-col>
        <a-col :xs="0" :sm="6">{{ type === 'file' ? getTime(itemData.time) : '-' }}</a-col>
        <a-col :xs="3" :sm="1" style="text-align: center">
          <a-popover v-model="popover" placement="topRight">
            <template slot="content">
              <a-button-group>
                <a-button
                  v-if="type === 'file'"
                  icon="login" size="small"
                  @click="openFile"
                >{{ breakpoint ? '' : '打开' }}
                </a-button>
                <a-button icon="rollback" size="small" @click="removeFile">{{ breakpoint ? '' : '移动' }}</a-button>
                <a-button icon="edit" size="small" @click="renameFile">{{ breakpoint ? '' : '重命名' }}</a-button>
                <a-button
                  v-if="type === 'file'"
                  icon="cloud-download" size="small"
                  @click="downloadFile"
                >{{ breakpoint ? '' : '下载' }}
                </a-button>
                <a-button icon="delete" size="small" @click="delFile">{{ breakpoint ? '' : '删除' }}</a-button>
              </a-button-group>
            </template>
            <a-button shape="circle" icon="ellipsis" size="small" @click.stop="popover = true"/>
          </a-popover>
        </a-col>
      </template>
    </a-row>
  </div>
</template>
<script>

import getTime from '@/utils/utils';

export default {
  name: 'FileItem',
  data: function () {
    return {
      getTime: getTime,
      popover: false
    }
  },
  props: {
    breakpoint: Boolean,
    itemData: Object,
    type: String,
    show: Boolean,
    index: Number,
    isChecked: Boolean
  },
  computed: {
    getFileSize: function () {
      const fileSizeByte = this.itemData.size
      let fileSizeMsg = ''
      if (fileSizeByte < 1048576) {
        fileSizeMsg = (fileSizeByte / 1024).toFixed(2) + 'KB'
      } else if (fileSizeByte === 1048576) {
        fileSizeMsg = '1MB'
      } else if (fileSizeByte > 1048576 && fileSizeByte < 1073741824) {
        fileSizeMsg = (fileSizeByte / (1024 * 1024)).toFixed(2) + 'MB'
      } else if (fileSizeByte > 1048576 && fileSizeByte === 1073741824) {
        fileSizeMsg = '1GB'
      } else if (fileSizeByte > 1073741824 && fileSizeByte < 1099511627776) {
        fileSizeMsg = (fileSizeByte / (1024 * 1024 * 1024)).toFixed(2) + 'GB'
      } else {
        fileSizeMsg = '文件超过1TB'
      }
      return fileSizeMsg
    }
  },
  methods: {
    chooseFileItem: function (e) {
      this.$emit('chooseFileItem', this.type, this.index, e.target.checked)
    },
    delFile: function () {
      this.$emit('delFile', this.type, this.index)
    },
    renameFile: function () {
      this.$emit('renameFile', this.type, this.index)
    },
    removeFile: function () {
      this.$emit('removeFile', this.type, this.index)
    },
    downloadFile: function () {
      this.$emit('downloadFile', this.type, this.index)
    },
    openFile: function () {
      this.$emit('openFile', this.type, this.index)
    }
  }
}
</script>
<style lang="less">
.fileItem {
  background-color: white;
  margin: 5px 0px;
  padding: 8px 0px 8px 10px;
  border: 1px solid @border-color-base;
  border-radius: @border-radius-base;

  .name {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .size {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

.fileItem:hover {
  border-color: @primary-color;
}
</style>
